@import url(https://fonts.googleapis.com/css?family=Gorditas:700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1.logo {
    font-family: 'Gorditas', cursive;
    display: inline-block;
    box-sizing: border-box;
    padding: 10px;
    border: none;
    color: #F08080;
    -webkit-text-decoration: normal;
            text-decoration: normal;
    text-align: center;
    text-overflow: clip;
    white-space: pre;
  }

  .logo:hover {
    text-shadow: 0 0 10px rgba(255,255,255,1) , 0 0 10px rgba(255,255,255,1) , 0 0 10px rgba(255,255,255,1) , 0 0 40px #FFD700 , 0 0 70px #FFD700 , 0 0 80px #FFD700 , 0 0 100px #FFD700 ;
  }

  a{
    color: #F08080;
  }

  h3.logo{
    font-family: 'Gorditas', cursive;
    display: inline-block;
    box-sizing: border-box;
    border: none;
    color: #F08080;
    -webkit-text-decoration: normal;
            text-decoration: normal;
    text-align: center;
    text-overflow: clip;
    white-space: pre;
  }

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.petCard {
  display: inline;
  background-repeat: no-repeat;
  background-position: center 90%;
  float: left;
  margin: 2%;
  padding: 1%;
  outline: #3B0B0B dotted thick;
  -webkit-transition: 0.75s;
  transition: 0.75s;
  opacity: 1;
}

ul{
 list-style-type: none;
 margin: 0px;
 padding: 0px;
 overflow: hidden;
 background-color: #D8C8B2;
}

li{
 margin: 0px;
 float: left;
 padding: 10px;
}

li a:hover{
  margin: 0px;
  padding: 10px;
  background-color: #B82647;
}

